@use 'variables' as v;
@use 'mixins' as m;
@use "sass:math";



.boton-pri {
    @include m.boton(v.$pri, inline-block);
}

.fondo-btn-negro {
    background-color: v.$negro;
    color: v.$blanco;
    &:hover {
        background-color: lighten(v.$negro, 10);

    }
}

.boton-transparente {
    @include m.boton(transparent, inline-block);
    box-shadow: none;
    position: relative;
    color: v.$negro;
    &::after {
        content: '';
        position: absolute;
        height: 2px;
        // width: calc(100% - 8rem);
        width: 100%;
        bottom: 0;
        left: 0;
        background-color: v.$negro;
        transition: v.$tra;
        transform: scaleX(0);
        transition: transform v.$tra;
        transform-origin: bottom right;
        // background-color: v.$pri;
       
    }

    &:hover {
        &::after {
            transform-origin: bottom left;
            transform: scaleX(1);

        }

    }
    
}

