@use 'variables' as v;
@use 'mixins' as m;
@use "sass:math";



.seccion {
    margin-top: 8rem!important;
}

.seccion-uno {
    margin-top: v.$separacion * 2!important;
}

.margin-top {
    margin-top: 8rem;

}
.contenido-centrado {
    max-width: 80rem;
}

.contenedor-secundario {
    width: 80%;
    max-width: 80rem;
    margin: 0 auto;
}

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-center-r {
    text-align: left;

    @include m.tablet {
        text-align: center;
    }
}

.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}

.w-100 {
    width: 100%;
}
.h-100 {
    height: calc(100vh - 20rem);
}
.flex-centrar {
    display: flex;
    justify-content: center;
    align-items: center;
}
.hacia-abajo {
    align-items: flex-end;
}
.displayNone {
    display: none;
}

.dos-columnas {
    @include m.grid(2, 2rem);
}

.dos-columnas-responsive {
    @include m.tablet {
        @include m.grid(2, 2rem);
    
    }
}

.no-margin {
    margin: 0;
}

.margen-derecha {
    &:not(:last-of-type) {
        margin-right: 2rem;

    }
}



.b-r-1 {
    border-radius: 1rem;
    overflow: hidden;
}


.no-margin-bottom {
    margin-bottom: 0;
}

.hidden {
    overflow: hidden
}

.margin-auto {
    margin: auto;
}

.m-l-r {
    margin-left: auto;
}


.grid-tres-columnas-responsive {
    @include m.telefono {
        @include m.grid(2, 2rem);
    }
    @include m.tablet {
        @include m.grid(3, 2rem);
        
    }
}

