.decoracion-titulos {
    &::before, &::after {
        font-family: v.$fuente_principal;
        // color: v.$gris-oscuro;
        color: lighten(v.$negro, 20);
    }
    &::before {
        content: "<";
        
    }
    &::after {
        content: "/>";
    }
}

.parrafo-destacado {
    background-color: v.$gris-claro;
    padding: 2rem;
    text-align: center;
    /* font-size: 2rem; */
    font-weight: v.$bold;
    transition: v.$tra;
}

