@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.fondo-gradiente {
    // background: linear-gradient(180deg, v.$blanco 0%, v.$ter 100%);
    background-color: v.$pri;

    min-height: 40rem;
    height: calc(100vh - 8rem);
    max-height: 70rem;
    // background-position: center 8rem;
    // background-attachment: fixed;
    // background-repeat: no-repeat;
    transition: v.$tra;
    position: relative;
    z-index: 1;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-image: url(/build/img/repeticion-fondo-computadora-codigo.svg);
        background-size: 150%;
        background-position: center;
        z-index: -1;
        background-attachment: fixed;
        transition: v.$tra;
        opacity: 1;
        @include m.tablet {
            background-size: 50%;
            
        }
    }
    
}


.imagen-principal {
    
    height: 35rem;
    width: 100%;
    position: relative;
    z-index: 1;

    // }
    img {
            /* bottom: 0; */
    -o-object-fit: contain;
    object-fit: contain;
    /* position: absolute; */
    /* right: 0; */
    width: 30rem;
    margin: auto;
    }

    &:hover .rectangulo-imagen {
        background-color: lighten(v.$pri, 10);
    }
    
}

.rectangulo-imagen {
    border-radius: 1rem;
    bottom: 35rem;
    height: 16rem;
    z-index: -1;
    // max-width: 35rem;
    position: relative;
    right: 0;
    width: 100%;
    background-color: v.$negro;
    transition: v.$tra;

}


