@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.pagina-no-encontrada {

    img {
        animation: rotacionColor 2s linear infinite;
    // animation: lds-dual-ring 1.2s linear infinite;
    
    }

    @keyframes rotacionColor {
        0% {
            filter: hue-rotate(0);
        }

        100% {
            filter: hue-rotate(360deg);
        }
    }
}

.footer-absolute {
    @include m.tablet {
        position: absolute;
        bottom: 0;
    }

}