.boton-transparente {
  background-color: transparent;
  color: #071914;
  font-weight: 800;
  text-decoration: none;
  padding: 2rem 4.5rem;
  text-align: center;
  margin: 2rem 0;
  display: inline-block;
  border: none;
  border-radius: 4rem;
  transition: 0.35s ease-in-out;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-shadow: none;
  position: relative;
  color: #071914;
}
.boton-transparente:hover {
  background-color: rgba(26, 26, 26, 0);
  cursor: pointer;
}
.boton-transparente::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #071914;
  transition: 0.35s ease-in-out;
  transform: scaleX(0);
  transition: transform 0.35s ease-in-out;
  transform-origin: bottom right;
}
.boton-transparente:hover::after {
  transform-origin: bottom left;
  transform: scaleX(1);
}

.dark .loader-fondo {
  background-color: #071914;
}
.dark .loader-fondo .newtons-cradle__dot::after {
  background-color: #FFFFFF;
}

.loader-fondo {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 9999;
  transition: 1s ease-in-out opacity, 1s ease-in-out visibility;
  opacity: 1;
  visibility: visible;
}
.loader-fondo .newtons-cradle {
  --uib-size: 6rem;
  --uib-speed: 1.4s;
  --uib-color: v.$negro;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--uib-size);
  height: var(--uib-size);
}
.loader-fondo .newtons-cradle__dot {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  width: 25%;
  transform-origin: center top;
}
.loader-fondo .newtons-cradle__dot::after {
  content: "";
  display: block;
  width: 100%;
  height: 25%;
  border-radius: 50%;
  background-color: #071914;
}
.loader-fondo .newtons-cradle__dot:first-child {
  animation: swing var(--uib-speed) linear infinite;
}
.loader-fondo .newtons-cradle__dot:last-child {
  animation: swing2 var(--uib-speed) linear infinite;
}
@keyframes swing {
  0% {
    transform: rotate(0deg);
    animation-timing-function: ease-out;
  }
  25% {
    transform: rotate(70deg);
    animation-timing-function: ease-in;
  }
  50% {
    transform: rotate(0deg);
    animation-timing-function: linear;
  }
}
@keyframes swing2 {
  0% {
    transform: rotate(0deg);
    animation-timing-function: linear;
  }
  50% {
    transform: rotate(0deg);
    animation-timing-function: ease-out;
  }
  75% {
    transform: rotate(-70deg);
    animation-timing-function: ease-in;
  }
}

