@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.contenedor-sobre-mi {
    width: 90%;
    max-width: 80rem;
    margin: 0 auto;
    @include m.tablet {
    width: 80%;
        
    }

    
}


.tecnologias-domino {
    @include m.grid(3, 2rem);
    .tecnologia {
        // padding: 2rem;
        border-radius: 1rem;
        box-shadow: 0px 0px 10px 0px v.$gris-claro;
        transition: v.$tra;
        position: relative;
        height: 9rem;
        overflow: hidden;
        @include m.flex-centrar;
        &:hover {
            box-shadow: 0px 0px 10px 0px darken(v.$gris-claro, 10);

        }

        &:hover p {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
        }

        &:hover img {
            transform: scale(0);
            opacity: 0;
        }

        img, p {
            // left: 50%; */
            position: absolute;
            /* top: 50%; */
            width: 100%;
            height: 100%;
            margin: 0;
            text-align: center;
            justify-content: center;
            display: flex;
            align-items: center;
            transition: v.$tra;
        }
        img {
            height: 5rem;
            
        }

        p {
            background-color: v.$pri;
            font-weight: v.$bold;
            border-radius: 1rem;
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
        }

    }
}


.estudios {

    .imagnes-fondo-blanco {
        background-color: v.$blanco;
        padding: 2rem;
        transition: v.$tra;
        // border-radius: 1rem;
        img {
            width: 50%;
        }
    }


}