@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.contenedor-formulario {
    width: 90%;
    max-width: 65rem;
    margin: 0 auto;
    @include m.tablet {
        width: 80%;
    }
}

//hola mundooo
.formulario {
    fieldset {
        padding: 0;
        border: none;

    }

    label {
        font-size: 1.4rem;
        color: v.$negro;
        margin-bottom: 1.4rem;
        transition: v.$tra;

        
    }

    span {
        //asterisco rojo (campo obligatorio)
        margin-left: 1rem;
        color: v.$rojo;
    }

    input, textarea {
        border: 1px solid v.$negro;
        transition: v.$tra;
        padding: 1rem 2rem;
        outline: none;
        width: 100%;
        font-size: 1.6rem;
        color: v.$negro;
        
        &::placeholder {
            color: v.$gris-oscuro;
        }

        &:focus {
            border-color: lighten(v.$negro, 30);

        }

        

        &[type="submit"] {
            border: none;
            padding: 2rem 4.5rem;
            width: auto;
            

        }

        
    }

    textarea {
        height: 20rem;
        resize: none;
        line-height: 1.5;
    }

    .form-input {
        display: grid;
        margin-bottom: 3rem;

        .sitio-web-si-no {

            display: flex;
            .check {
                display: flex;
                justify-content: left;
                align-items: center;
                label {
                    margin: 0;
                }
                input {
                    // margin-bottom: 1.4rem;
                    // width: auto;
                    margin: 0 3rem 0 1rem;
                    height: 2.5rem;
                    width: 2.5rem;
                    transition: none;


                    &:checked {
                        // filter: hue-rotate(247deg) opacity(0.6);
                        accent-color: v.$pri;
                        color: v.$pri;
                    }
                }
            }
        }

        .validacion-error {


            // height: auto;
            height: 0;
            visibility: hidden;
            opacity: 0;
            transition: v.$tra;
            p {
                color: v.$rojo;
                margin-bottom: 0;
    
                i {
                    margin-right: 1rem;
                }
            }
        } 

    }
}



// aade la validacion del formulario cuando se da click en el boton enviar
.anadir-validacion {
    
    #nombre, #apellido, #telefono, #correo, #negocio, #sitio, #presupuesto, #mensaje {
        &:invalid {
            border-color: v.$rojo;
        }
    }

    // #nombre {
    //     &:invalid {
    //         &.nombre {
    //             opacity: 1;
    //             visibility: visible;
    //         }
    //     }
    // }

    #nombre:invalid ~ .nombre {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #apellido:invalid ~ .apellido {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #telefono:invalid ~ .telefono {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #correo:invalid ~ .correo {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #negocio:invalid ~ .negocio {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    .check:required ~ .sitio-cuentas {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #sitio:invalid ~ .sitio {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #presupuesto:invalid ~ .presupuesto {
        opacity: 1;
        visibility: visible;
        height: auto;

    }

    #mensaje:invalid ~ .mensaje {
        opacity: 1;
        visibility: visible;
        height: auto;

    }
}

.acciones {

    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 3rem;
    margin-bottom: v.$separacion;

    a {
        padding: 2rem 0;
        margin: 0;
    }

}

.modal-formulario {
    position: fixed;
    top: 10rem;
    background-color: v.$blanco;
    padding: 4rem 2rem;
    z-index: 100;
    overflow: auto;
    height: calc(100vh - 16rem);
    transition: v.$tra;
    opacity: 0;
    visibility: hidden;
    transform: scale(0);

    .formulario {

        &.curriculum {
            
            legend {
                font-size: 3rem;
                font-weight: v.$bold;
                font-family: v.$fuente_secundaria;
                margin-bottom: 3rem;
                transition: v.$tra;
            }

            .form-input {

                &:not(:last-of-type) {
                    // border-bottom: 1px solid lighten(v.$curri-negro, 10);
                    padding-bottom: 1rem;

                }
                .input-habilidad {

                    

                    input {
                        appearance: none;
                        height: 1rem;
                        width: 100%;
                        accent-color: v.$curri-pri;
                        transition: v.$tra;
                        outline: none;
                        background-color: lighten(v.$curri-pri, 50);
                        cursor: pointer;
                        border: none;
                        border-radius: 1rem;
                    }

                    
                }
            }

            .boton-pri {
                background-color: v.$curri-pri;
                color: v.$blanco;
                &:hover {
                    background-color: lighten(v.$curri-pri, 10);
                }
            }

            .boton-pri, .boton-transparente {
                margin-bottom: 0;
                margin-right: 2rem;
            }

            .numero-html, .numero-css, .numero-js, .numero-php, .numero-gulp, .numero-sass, .numero-adobe-xd, .numero-mysql {
                transition: v.$tra;
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                background-color: v.$curri-pri;
                color: v.$blanco;
                padding: 2rem;
                font-size: 1rem;
                font-weight: v.$bold;
                @include m.flex-centrar;
            }


        }

    }
}

.animar {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
}

.cerrar {
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
}

.hacer-hover-editar {
    transition: v.$tra;
    &:hover {
        background-color: v.$gris-claro;
    }
}
#ari {

    .swal2-container.swal2-center.swal2-backdrop-show {
        background-color: v.$curri-pri-trans;
    }

    .swal2-confirm  {
        &.swal2-styled {
            @include m.boton(v.$curri-pri, inline-block);
            font-size: 1.5rem;
            color: v.$blanco;
            transition: v.$tra;

        }
    }

    .swal2-popup.swal2-modal.swal2-icon-success.swal2-show {
        background-color: v.$blanco;

        #swal2-title {
            color: v.$curri-negro;
            font-size: 2rem;
            // background-color: v.$curri-pri;
        }

        #swal2-html-container {
            color: v.$curri-negro;

        }

        
    }

    .swal2-success-ring {
        // border: none;
        border-color: v.$curri-pri;
    }

    .swal2-icon.swal2-success [class^=swal2-success-line] {
        background-color: v.$curri-pri;
    }



}





