@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";



.entrada {
    height: 100%;
    min-height: auto;
    max-height: 100%;
    padding: 2rem;
    // background-attachment: scroll;
    // background-position: center center;
    background: none;
    border-bottom: 1px solid v.$gris-oscuro;
    transition: v.$tra;
    // width: ;
}

.mini-seccion {
    margin-top: 4rem;
}

.imagen-proyecto {
    box-shadow: 4px 4px 15px 0px v.$gris-claro;
    transition: v.$tra;
}

.grid-proyectos {

    

    &:not(:last-of-type){
        border-bottom: 1.5px solid v.$gris-claro;
    }

    @include m.tablet {
        display: grid;
        // grid-template-columns: repeat(75%, 25%);
        // @include m.grid(2, 2rem);
        // @include m.grid(2, 2rem);
        grid-template-columns: 60% 1fr;
        column-gap: 2rem;
        row-gap: 2rem;
        gap: 2rem;
        padding-bottom: 1rem;

        &:nth-child(2n) {
        grid-template-columns: 1fr 60%;

            .datos-proyectos {
                grid-column: 1/2;
                grid-row: 1 / 2;
        
            
        
            }
        }
    }
    
    

    
}
