@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.dark {
    
    // .loader {
    //     background-color: v.$negro;

    //     .spin,
    //     .lds-dual-ring {
    //         filter: invert(1);
    //     }
    // }

    .barra-nav {
        background-color: v.$negro;
        
        img {
            filter: invert(1);
        }
    }

    .fondo-gradiente {
        
        // background: linear-gradient(180deg, v.$negro, darken(v.$pri, 70));
        // background-color: v.$negro;
        background-color: #093328;
        // background-attachment: fixed;
        // background-position: center 8rem;
        // background-repeat: no-repeat;
        &::after {
            opacity: .04;
        }
    }
    

    .datos-principales {
        p {
            opacity: .7;
        }
        .boton-transparente {
            opacity: 1;
        }
    }

    // .nav-hijos:after {
    //     background-color: v.$pri-o;
    // }

    .span {
        background-color: v.$blanco;
        opacity: .7;
    }


    h1, h2, h3, h4, legend {
        color: darken(v.$blanco, 2);
    }

    p, a,
    .formulario label {
        color: v.$blanco;
        opacity: .7;
    }

    .boton-pri {
        background-color: v.$pri-o;
        color: v.$negro;
        opacity: 1;
        // box-shadow: 0px 0px 15px darken(v.$pri-o, 8);
        box-shadow: 4px 3px 17px 0px rgba(24, 214, 163, 0.5);
        // box-shadow: none;
        &:hover {
            background-color: lighten(v.$pri-o, 15);
            box-shadow: 4px 3px 17px 0px rgba(24, 214, 163, 0.5);

            // box-shadow: 0px 0px 15px 0px darken(v.$pri, 50);

        }
    }

    .boton-transparente {
        &::after {
            background-color: v.$blanco;
            // opacity: .7;
        }

        
    }

    .rectangulo-imagen {
        background-color: v.$pri-o;

        
    }

    .imagen-principal {
    
        
        &:hover .rectangulo-imagen {
            // background-color: lighten(v.$pri-o, 5);
            background-color: lighten(v.$pri-o, 15);


        }
        
    }

    .barra-nav {

        box-shadow: 0 2px 10px lighten(v.$negro, 3);

        a {
            color: v.$blanco;
            opacity: .7;
        }
        
    }

    .pantallaCompleta {
        background-color: darken(v.$pri-trans, 70);

    }

    // @include m.tablet {
    //     .barra-nav a {
    //         color: v.$blanco;
    //         opacity: .7;
    //     }
    // }

    .muestra {
        background-color: v.$negro;
    }

    //body color
    background-color: v.$negro;
    //fin

    // .svg-titulos {
    //     &::before {
            
    //         opacity: .3;
    //         filter: grayscale(1);
    //     }
    // }

    .sitios-web { 
        box-shadow: 0 0 15px 0 lighten(v.$negro, 3);
        &:hover {
            box-shadow: 0 0 15px 0 lighten(v.$negro, 6);
        }

        &:hover h3 span {
            color: v.$negro
        }

        ul {
            color: v.$blanco;
            opacity: .7;
        }
    
    }


    .grid-proyectos {

        &:not(:last-of-type){
            border-color: v.$gris-oscuro;
        }
    }

    .card {
        box-shadow: 0 0 15px 0 lighten(v.$negro, 3);

        i {
            background-color: v.$pri-o;
            color: v.$negro;       
        }

        &:hover {
            box-shadow: 0 0 15px 0 lighten(v.$negro, 6);

        }
    }

    .card-principal {
        background-color: v.$pri-o;
        box-shadow: 0px 0px 10px 0px lighten(v.$pri-o, 10);
        h3, p {
            color: v.$negro;
            opacity: 1;
        }

        &:hover {
            box-shadow: 0px 0px 10px 0px darken(v.$pri-o, 15);

        }
    }

    &::-webkit-scrollbar {
        // width: 2rem;
        // height: 1rem;
        background-color: lighten(v.$negro, 10);
    
    }
    
    &::-webkit-scrollbar-thumb {
        background-color: darken(v.$negro, 3);
        // border-radius: 1rem;
        // height: 10rem;
        // box-shadow: 0 0 6px 2px v.$gris-claro inset;
    }

    .testimoniales {
        &::-webkit-scrollbar {
            // width: 2rem;
            // height: 1rem;
            background-color: lighten(v.$negro, 10);
        
        }
        
        &::-webkit-scrollbar-thumb {
            background-color: darken(v.$negro, 3);
            // border-radius: 1rem;
            // height: 10rem;
            // box-shadow: 0 0 6px 2px v.$gris-claro inset;
        }
    }


                                                                            

    .imagen-proyecto {
        box-shadow: 4px 4px 15px 0px lighten(v.$negro, 5);
    }

    .estudios {

        .imagnes-fondo-blanco {
            background-color: #b9b9bc;
            // padding: 2rem;
            a {
                opacity: 1;
            }
           
        }
    
    
    }
    
    .parrafo-destacado {
        background-color: #093328;

    }

    .tecnologias-domino {

        .tecnologia {
            box-shadow: 0 0 15px 0 lighten(v.$negro, 3);
            // transition-delay: .3s;
        // box-shadow: 0px 0px 10px 0px lighten(v.$pri-o, 10);
            p {
                color: v.$negro;
            }
            // &:hover {
            //     // background-color: v.$pri-o;
            //     // transition-delay: 0;
                

            //     // box-shadow: 0 0 15px 0 lighten(v.$negro, 6);
            //     box-shadow: 0px 0px 10px 0px darken(v.$pri-o, 15);
                
    
            // }

        }

    }

    .formulario input, .formulario textarea {
        background-color: lighten(v.$negro, 10);
        color: v.$blanco;
        opacity: .7;

        &::placeholder{
            color: v.$blanco;
            opacity: .7;
        }
    }

    .formulario input[type=submit] {
        // background-color: v.$pri-o;
        // color: v.$negro;
        // box-shadow: 0px 0px 15px darken(v.$pri-o, 8);
        // &:hover {
        //     background-color: lighten(v.$pri-o, 10);

        //     // box-shadow: 0px 0px 15px 0px darken(v.$pri, 50);

        // }

        background-color: v.$pri-o;
        color: v.$negro;
        opacity: 1;
        // box-shadow: 0px 0px 15px darken(v.$pri-o, 8);
        box-shadow: 4px 3px 17px 0px rgba(24, 214, 163, 0.5);
        // box-shadow: none;
        &:hover {
            background-color: lighten(v.$pri-o, 15);
            box-shadow: 4px 3px 17px 0px rgba(24, 214, 163, 0.5);

            // box-shadow: 0px 0px 15px 0px darken(v.$pri, 50);

        }
    }

    .ventanaContacto {
        background-color: v.$negro;
        .redes-sociales {
            color: v.$blanco;
            opacity: .7;
        }
    }

    .fondo-curriculum {
        background-color: v.$negro;
        
    }

    .contenedor-curriculum {
        box-shadow: none;
        @include m.telefono {
            box-shadow: 3px 6px 13px lighten(v.$curri-negro, 5);
            
        }
    }

    .secciones-curriculum-fondo {
        background-color: lighten(v.$curri-negro, 8);

    }

    .curriculum-perfil-experiencia {
        background-color: lighten(v.$curri-negro, 8);
        h3,h5 {
            color: v.$blanco;
        }

        h4 {
            color: lighten(v.$curri-pri, 10);
        }

        p {
            color: v.$blanco;
            opacity: .7;
        }

        .boton-pri {
            color: v.$curri-negro;
            box-shadow: 0 0 15px darken(v.$curri-pri, 15);
            &:hover {
                background-color: lighten(v.$curri-pri, 10);

            }
        }

    }

    .nombre-curriculum {

    
        h1, h2 {
            color: v.$curri-negro;

        }

    }

    .habilidades-software {
        background-color: lighten(v.$curri-negro, 8);

        h3, .barra-display p {
            color: v.$blanco;
        }

        li {
            &::before {
                filter: invert(1);
            }

            .progreso {

                
                &::-webkit-progress-bar {
                    background-color: lighten(v.$curri-negro, 6);

                }
                &::-webkit-progress-value{
                    background-color: v.$curri-pri;
                }
            }
        }

    }

    .hacer-hover-editar {
        // transition: v.$tra;
        &:hover {
            background-color: lighten(v.$curri-negro, 11);

        }
    }

    .modal-formulario {

        background-color: lighten(v.$curri-negro, 8);
        &::-webkit-scrollbar {
            background-color: lighten(v.$negro, 10);
        
        }
        
        &::-webkit-scrollbar-thumb {
            background-color: darken(v.$negro, 3);

        }

        .formulario {
    
            &.curriculum {
    
                .form-input {
    
                    textarea {
                        border-color: transparent;
                        &:focus {
                            border-color: v.$curri-pri;
                        }

                       
                    }
                    .numero-html, .numero-css, .numero-js, .numero-php, .numero-gulp, .numero-sass, .numero-adobe-xd, .numero-mysql {
                        color: v.$curri-negro;
                        // opacity: .7;
                    }
                }

                .boton-pri {
                    
                    color: v.$curri-negro
                   
                }
            }

            .boton-transparente {
                color: v.$blanco;
                
            }
        }
    }

   
    

    

}

#ari {
        
    .dark {

        .swal2-container.swal2-center.swal2-backdrop-show {
            background-color: v.$curri-pri-trans;
        }

        .swal2-confirm  {
            &.swal2-styled {

                color: v.$curri-negro
                // transition: v.$tra;
    
            }
        }
    
        .swal2-popup.swal2-modal.swal2-icon-success.swal2-show {
            background-color: v.$curri-negro;
    
            #swal2-title {
                color: v.$blanco
                // font-size: 2rem;
                // background-color: v.$curri-pri;
            }
    
            #swal2-html-container {
                color: v.$blanco
    
            }
    
            
        }

        .swal2-icon.swal2-success [class^=swal2-success-line] {
            background-color: v.$curri-pri;
        }

        .swal2-success-circular-line-right, .swal2-success-circular-line-left, .swal2-success-fix {
            background-color: v.$curri-negro!important;

        }



    }



}