@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.fondo-curriculum {
    
    background-color: v.$curri-gris;
    padding: v.$separacion * 2 0 4rem;
    transition: v.$tra;
    .datos-contacto-educacion {
        background-color: v.$curri-negro;
    }
}

.contenedor-curriculum {
    
    margin: 0 auto;
    box-shadow: 3px 6px 13px v.$gris-claro;
    transition: v.$tra;
    @include m.telefono {
        width: 80%;
        max-width: 80rem;
    }

    // padding-bottom: 4rem;
    h1, h2, h3, h4, h5, p, a, span {
        font-family: v.$fuente_curriculum;
        color: v.$curri-negro;
    }

    p {
        font-weight: 400;
    }

    .boton-pri {
        background-color: v.$curri-pri;
        color: v.$blanco;
        margin-bottom: 0;
        &:hover {
            background-color: darken(v.$curri-pri, 10);

        }
    }

    display: grid;
    
    @include m.tablet {
        // display: grid;
        grid-template-columns: 30% 1fr;
    }
    
}

.datos-contacto-educacion {

    .lineas-decoracion-imagen {
        // width: 18rem;
        // height: 18rem;
        aspect-ratio: 1 / 1;
        border-radius: 50%;
        overflow: hidden;
        margin: 5rem auto;
        position: relative;
        border: .5rem solid v.$curri-pri;
        width: 90%;
        // transform: scale(1.2);

        img {
            // width: 16rem;
            // height: 16rem;
            aspect-ratio: 1 / 1;
            -o-object-fit: contain;
            object-fit: contain;
            margin: auto;
            padding: .5rem;
            border-radius: 50%;
            background-color: v.$curri-pri-trans;
        }

        // @include m.tablet {
        //     // width: 16rem;
        //     // height: 16rem;
        //     margin: 7rem auto;
        //     // transform: none;
        //     img {
        //         // width: 14rem;
        //         // height: 14rem;
        //     }
        // }
        // @include m.solo-telefono {
        //     top: 3.5rem;
        // }
        
        
    }

    .contacto-curriculum {
        margin: 2.5rem 0;
        .enlace-curriculum {
            justify-content: left;
            border-left: .5rem solid transparent;
            padding-left: 1rem;
            transition: v.$tra;
            &:hover {
                border-color: v.$curri-pri;
                background-color: darken(v.$curri-pri-trans, 30);

            }

            
            img {
                height: 1.6rem;
                width: 1.6rem;
                object-fit: contain;
            }

            p {
                color: v.$blanco;
                margin-left: 1rem;

                width: calc(100% - 3rem);
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
        }
    }

    .educacion-curriculum {
        // margin: 2.5rem 0;
        padding-bottom: 2.5rem;

        .decoracion-titulos-curriculum {
            margin-bottom: 2.5rem;
        }

        .estudios-fecha {

            &:not(:last-of-type) {
                border-bottom: 1px solid v.$curri-pri-trans;
            }
            span, p {
                color: v.$blanco;
            }
            span {
                font-weight: v.$bold;
                padding: 1rem 1.5rem;
                display: inline-block;
                // margin-bottom: 1rem;
                width: 100%;
                transition: v.$tra;
            }

           

            a {
                display: inline-block;
                border-left: .5rem solid transparent;
                padding-left: 1rem;
                padding-right: 1.5rem;
                transition: v.$tra;
                @include m.solo-telefono {
                    width: 100%;
                }
                &:hover {
                    border-color: v.$curri-pri;
                    background-color: darken(v.$curri-pri-trans, 30);
                }

                p {
                    // margin: 1rem 1.5rem;
                }
            }

            
        }
    }

}

.decoracion-titulos-curriculum {
    padding: 1rem 0;
    border-left: 1rem solid v.$curri-pri;
    background-color: v.$curri-pri-trans;
    h3 {
        color: v.$blanco;
        margin: 0 0 0 2rem;
        text-transform: uppercase;
        font-size: 2.6rem;
        @include m.tablet {
            font-size: 2rem;
            
        }
    }
}

.secciones-curriculum-fondo {
    background-color: v.$blanco;
    padding-bottom: 4rem;
    transition: v.$tra;
}

.seccion-nombre-tablet {
    display: none;
    @include m.tablet {
        display: block;
    }
}

.seccion-nombre-telefono {
    display: none;
    @include m.solo-telefono {
        display: block;
    }
}

.nombre-curriculum {
    @include m.solo-telefono {
        padding: 5rem 0;
        margin-top: 0;

    }
    background-color: v.$curri-pri;
    padding: 3rem 0;
    margin-top: 5rem;

    h1, h2 {
        color: v.$blanco;
        text-transform: uppercase;
        margin: 0;
    }

    h2 {
        font-weight: v.$regular;
        font-size: 2rem;
    }
}

.curriculum-perfil-experiencia {
    background-color: v.$blanco;
    padding-top: 5rem;
    transition: v.$tra;

    .parte-contenido {

        &:not(:last-of-type){
            margin-bottom: 5rem;
            
        }
        @include m.solo-telefono {
            &:last-of-type {
                padding-bottom: 5rem;

            }
        }
    }
    h3 {
        margin-top: 0;
        font-size: 2.6rem;
        text-transform: uppercase;
        @include m.tablet {
            font-size: 2rem;
            
        }
    }

    h4, h5 {
        transition: v.$tra;
        font-size: 1.4rem;
        margin: 1.5rem 0;
    }

    h4 {
        color: v.$curri-pri;
        text-transform: uppercase;
    }

    

    p {
        color: v.$curri-negro;
    }
}

.habilidades-software {
    background-color: v.$blanco;
    padding-top: 5rem;
    transition: v.$tra;

    h3 {
        margin-top: 0;
        text-transform: uppercase;
    }

    ul {
        padding: 0;

        li {
            position: relative;
            list-style: none;
            transition: v.$tra;
            &::before {
                // list-style-image: url('/arigomez/public/build/img/curriculum/html.svg');
                content: '';
                width: 2rem;
                height: 4rem;
                position: absolute;
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                transition: v.$tra;
                // background-image: url('/build/img/curriculum/html.svg');

            }


            &:nth-child(1)::before {
                background-image: url('/build/img/curriculum/html.svg');
            }
            &:nth-child(2)::before {
                background-image: url('/build/img/curriculum/css.svg');
            }
            &:nth-child(3)::before {
                background-image: url('/build/img/curriculum/js.svg');
            }
            &:nth-child(4)::before {
                background-image: url('/build/img/curriculum/php.svg');
            }
            &:nth-child(5)::before {
                background-image: url('/build/img/curriculum/gulp.svg');
            }
            &:nth-child(6)::before {
                background-image: url('/build/img/curriculum/sass.svg');
            }
            &:nth-child(7)::before {
                background-image: url('/build/img/curriculum/adobe-xd.svg');
            }
            &:nth-child(8)::before {
                background-image: url('/build/img/curriculum/mysql.svg');
            }
                
            
            

            .barra-display {
                display: grid;
                @include m.grid(2, 1rem);

                @include m.tablet {
                    grid-template-columns: 30% 1fr;
                    
                }

            }

            p {
                display: inline-block;
                // width: 100%;
                margin-left: 4rem;
            }

            .progreso {
                width: 100%;
                height: 1rem;

                -webkit-appearance: none;
                -moz-appearance: none;
                -o-appearance: none;
                -ms-appearance: none;
                     appearance: none;
                
                    //  , ::-moz-progress-bar, ::-o-progress-bar, ::-ms-progress-bar
                &::-webkit-progress-bar {
                    background-color: v.$curri-gris;
                    transition: v.$tra;

                }
                // , ::-moz-progress-value, ::-o-progress-value, ::-ms-progress-value
                &::-webkit-progress-value {
                    background-color: v.$curri-pri;
                }
            }
        }
    }
}

.iniciar-sesion-curriculum {
    @include m.flex-centrar;
    flex-wrap: wrap;
    padding-top: 3rem;

    @include m.solo-telefono {
        justify-content: left;
    }

    a {
        padding-bottom: 2rem;
        padding-top: 0;
        margin-bottom: 0;
    }
}