@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.whatsapp {
    position: fixed;
    right: 5%;
    bottom: 5%;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: v.$verde;
    transition: v.$tra;
    box-shadow: 0 2px 10px lighten(v.$verde, 10);
    opacity: 1!important;

    i {
        font-size: 3rem;
        color: v.$blanco;
    }

    &:hover {
        background-color: lighten(v.$verde, 10);

    }
}

.paypal {
    width: 50%;
    margin: 1.5rem 0;
    @include m.tablet {
        width: 15rem;
        
    }
}