@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.sitios-web {

    padding: 2rem;
    box-shadow: 0px 0px 10px 0px v.$gris-claro;
    transition: v.$tra;
    position: relative;
    padding-bottom: 14rem;
    margin-bottom: 2rem;

    @include m.tablet {
        margin: 0;
    }
    &:hover {
        box-shadow: 0px 0px 10px 0px darken(v.$gris-claro, 10);

    }

    h3 {
        font-family: v.$fuente_principal;
        text-align: center;
        margin-top: 1rem;
        span {
            display: block;
            width: 100%;
            position: relative;
            text-overflow: ellipsis;
            overflow: hidden;
            transition: v.$tra;
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 1rem;
                background-color: v.$pri;
                height: 100%;
                transition: v.$tra;
                z-index: -1;
            }
        }
    }

    &:hover h3 span::before {
        width: 100%;
    }


    ul {
        padding: 2rem;
        transition: v.$tra;
        li {

            &:not(:last-of-type) {
                margin-bottom: 1rem;
            }
        }
    }



    .precio-boton {
        position: absolute;
        height: 14rem;
        bottom: 0rem;
        width: 100%;
        h4 {
            margin: 0;
            font-size: 2rem;
            span {
                //mxn aprox.
                font-size: 1rem;
                margin-left: 1rem;
            }
        }
        .boton-pri {
            width: calc(100% - 4rem);
        }
        

    }
}